export default {
	data() {
		return {
			currentNum:1,
			stu:null,
			dataList:null,
			pageType:null
		}
	},
	methods: {
		next()
		{
			this.currentNum++;
			this.birthdayRemindData()
			
			
		},
		prevent()
		{
			this.currentNum--;
			this.birthdayRemindData()
		},
		
		birthdayRemindData()
		{
			uni.request({
				url:this.serverUrl + '/stu/birthday/' + this.pageType + '/' + this.currentNum + '?limit=1',
				method:"GET",
				data:{},
				success: birthdayData =>{
					console.log(birthdayData)
					this.stu = birthdayData.data[0]
				},
				fail: () => {},
				complete: () => {},
			})
		},
	},
	computed:{
		hasPrevent:function(){
			return{
				'bg-red': true
			}
		}
	},
	onShow(param) {
		var me = this;
		this.birthdayRemindData()
			
	},
	onLoad(param)
	{
		console.log("load... type:" + param.type)
		this.pageType = param.type
		
	}
	
}
